<template>
    <v-card 
          style="margin:5px;box-sizing:border-box;display:inline-block;width:240px;max-width:calc(50% - 10px)"
          cols="4"
          class="deck-card"
    >
        <v-btn :to="`/deck?code=${deck.deckCode}`" class="deck" :style="`flex-basis:0;width:100%;height:260px;background-image:url('assets/${deck.signatureChampion.code}-full.png');background-position:50% 0;background-size:cover;padding:0px;border-radius: 15px;overflow: hidden;`">
            <h2>{{ deck.archetypeName }}</h2>
            <v-row align-content="center" justify="center" style="padding-right:20px;;position:absolute;bottom:120px;width:100%;box-sizing:border-box">
            <v-col style="padding:0; padding-top:20px" cols="3" v-for="region in deck.factions" :key="region.index">
                <v-avatar size="62" >
                <v-img :src="factionImg[region]"/>
                </v-avatar>
            </v-col>
            </v-row>
            <v-row align-content="center" justify="center" style="padding-right:10px;position:absolute;bottom:60px;width:100%;box-sizing:border-box">
            <v-col style="padding:0;padding-top:20px" cols="2" v-for="champion in deck.champions" :key="champion.index">
                <v-avatar size="44">
                <v-img :src="`assets/${champion.code}-full.jpg`" style="border:2px solid #000"/>
                </v-avatar>
            </v-col>
            </v-row>
            <v-row style="margin-top:15px;position:absolute;bottom:10px;width:100%" align-content="center" justify="center">
            <v-col style="padding:0;padding-top:10px;text-align:center" cols="6">
                <span>
                {{deck.winrate}}% WR
                <br/>
                {{deck.games}} games
                </span>
            </v-col>
            </v-row>
        </v-btn>
    </v-card>
</template>
<script>
export default {
    props: {
        deck: { type: Object }
    },
    data () {
        return {
            factionImg: {
                IO: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Ionia.png",
                PZ: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/PitoverZaun.png",
                DE: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Demacia.png",
                NX: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Noxus.png",
                FR: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Freljord.png",
                SI: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/ShadowIsles.png",
            },
        }
    }
}
</script>
<style>
  .deck {
    border: 1px solid #535353;
    box-shadow: 0px 2px 7px rgba(0,0,0,0.8) !important;
  }
  .deck .v-btn__content {
    display:block;
    width:100%;
    height:260px;
    background-image: linear-gradient(to bottom, rgba(38,50,56,0), rgba(8,4,15,1) 90%);
  }
  .deck h2, .deck .wr-row {
    background: rgba(0,0,0,0.25);
    font-size: 1rem !important;
    padding: 12px 20px !important;
    white-space: break-spaces !important;
    text-align:center;
  }
  .deck .row {
    margin-left: 0px !important;
    margin-right: 0px !important;
  }
</style>