<template>
    <v-app style="z-index:2;background:transparent">
        <Navbar />
        <div style="height:calc(100vh - 48px);overflow-y:scroll;overflow-x:hidden;position:relative">
            <v-row class="home-row">
                <v-col cols="7" :class="`home-card-decks home-card-bp-${$vuetify.breakpoint.name}`">
                    <div class="home-card">
                        <v-card-title>Best Performing Decks</v-card-title>
                        <div class="home-card-content">
                            <template v-if="isLoading">
                                <div class="loader" style="background-image:url('assets/loader.gif')"></div>
                            </template>
                            <template v-else>
                                <v-row v-for="n in 5" :key="n">
                                    <v-col cols="6" v-for="(deck, index) in decks.slice((n*2-2),(n*2))" :key="index">
                                        <v-btn :to="`/deck?code=${deck.deckCode}`" class="home-item" :style="`background-image:url('assets/${deck.signatureChampion.code}-full.jpg')`">
                                            <v-tooltip right>
                                                <template v-slot:activator="{ on }">
                                                    <span v-on="on" class="home-card-tooltip-wrap">
                                                        <div class="home-item-title"><b>{{n*2+index-1}}.</b> {{deck.archetypeName}}</div>
                                                        <div class="factions">
                                                            <template v-for="faction in deck.factions.slice(0,2)">
                                                                <div class="home-item-avatar-pre" :key="faction.index">
                                                                    <v-avatar size="24">
                                                                        <v-img :src="factionImg[faction]" />
                                                                    </v-avatar>
                                                                </div>
                                                            </template>
                                                        </div>
                                                        <div class="champions">
                                                            <template v-for="champion in deck.champions.slice(0,2)">
                                                                <div class="home-item-avatar" :key="champion.index">
                                                                    <v-avatar>
                                                                        <v-img :src="`assets/${champion.code}-full.jpg`" />
                                                                    </v-avatar>
                                                                </div>
                                                            </template>
                                                        </div>
                                                    </span>
                                                </template>
                                                <span>
                                                    <DeckCard :deck="deck" />
                                                </span>
                                            </v-tooltip>
                                        </v-btn>
                                    </v-col>
                                </v-row>
                            </template>
                        </div>
                    </div>
                </v-col>
                <v-col cols="5" :class="`home-card-cards home-card-bp-${$vuetify.breakpoint.name}`">
                    <div class="home-card">
                        <v-card-title>Best Performing Cards</v-card-title>
                        <div class="home-card-content">
                            <template v-if="isLoadingCards">
                                <div class="loader" style="background-image:url('assets/loader.gif')"></div>
                            </template>
                            <template v-else>
                                <v-row v-for="n in 5" :key="n">
                                    <v-col cols="6" v-for="(card, index) in cards.slice((n*2-2),(n*2))" :key="index">
                                        <v-btn :to="`/card?code=${card.cardCode}`" class="home-item" :style="`background-image:url('assets/${card.cardCode}-full.jpg')`">
                                            <v-tooltip left>
                                                <template v-slot:activator="{ on }">
                                                    <span v-on="on" class="home-card-tooltip-wrap">
                                                        <div class="home-item-title home-item-title-card"><b>{{n*2+index-1}}.</b> {{card.name}}</div>
                                                        <div class="factions">
                                                            <div class="home-item-avatar-pre">
                                                                <v-avatar size="24">
                                                                    <v-img :src="factionImg[factionRefs[card.regionRef]]" />
                                                                </v-avatar>
                                                            </div>
                                                        </div>
                                                        <div class="cost">
                                                            {{card.cost}}
                                                        </div>
                                                    </span>
                                                </template>
                                                <span>
                                                    <CardCard :card="card" />
                                                </span>
                                            </v-tooltip>
                                        </v-btn>
                                    </v-col>
                                </v-row>
                            </template>
                        </div>
                    </div>
                </v-col>
            </v-row>
            <v-row class="home-row">
                <v-col cols="5" :class="`home-card-regions home-card-bp-${$vuetify.breakpoint.name}`">
                    <div class="home-card">
                        <v-card-title>Regions Statistics</v-card-title>
                        <div class="home-card-content home-regions">
                            <template v-if="isLoading">
                                <div class="loader" style="background-image:url('assets/loader.gif')"></div>
                            </template>
                            <template v-else>
                                <template v-for="region in regions">
                                    <div :key="region.index" class="home-region">
                                        <v-avatar>
                                            <v-img :src="factionImg[region]" />
                                        </v-avatar>
                                        <v-row>
                                            <v-col cols="4">{{regionNames[region]}}</v-col>
                                            <v-col cols="3">Wins: {{factionsStats[region].w}}</v-col>
                                            <v-col cols="3">Losses: {{factionsStats[region].l}}</v-col>
                                            <v-col cols="2">W/L: {{factionsStats[region].wr}}%</v-col>
                                        </v-row>
                                        <v-row>
                                            <v-progress-linear :color="regionColors[region]" height="10" :value="factionsStats[region].wr" striped />
                                        </v-row>
                                    </div>
                                </template>
                            </template>
                        </div>
                    </div>
                </v-col>
                <v-col cols="4" :class="`home-card-players home-card-bp-${$vuetify.breakpoint.name}`">
                    <div class="home-card">
                        <v-card-title>Best Players</v-card-title>
                        <div class="home-card-content">
                            <template v-if="isLoading">
                                <div class="loader" style="background-image:url('assets/loader.gif')"></div>
                            </template>
                            <template v-else>
                                <v-row v-for="n in 5" :key="n">
                                    <v-col cols="6" v-for="(player, index) in players.slice((n*2-2),(n*2))" :key="index">
                                        <v-btn :to="`/player?id=${player.id}`" class="home-item player-home-item">
                                            <div class="home-item-title home-item-title-player"><b>{{n*2+index-1}}.</b> {{player.name}}</div>
                                            <v-avatar><v-icon>mdi-user</v-icon></v-avatar>
                                            <div class="home-player-winrate">W/L: {{player.wr}}%</div>
                                        </v-btn>
                                    </v-col>
                                </v-row>
                            </template>
                        </div>
                    </div>
                </v-col>
                <v-col cols="3" :class="`home-card-global home-card-bp-${$vuetify.breakpoint.name}`">
                    <div class="home-card">
                        <v-card-title>Global Statstics</v-card-title>
                        <div class="home-card-content home-global-stats">
                            <template v-if="isLoading">
                                <div class="loader" style="background-image:url('assets/loader.gif')"></div>
                            </template>
                            <template v-else>
                                <v-row>
                                    <v-col cols="8">Decks:</v-col>
                                    <v-col>{{decks.length}}</v-col>                        
                                </v-row>
                                <v-row>
                                    <v-col cols="8">Games:</v-col>
                                    <v-col>{{gamesCount}}</v-col>                        
                                </v-row>
                                <v-row>
                                    <v-col cols="8">Players:</v-col>
                                    <v-col>{{players.length}}</v-col>                        
                                </v-row>
                                <v-row>
                                    <v-col cols="8">Overall Winrate of players using LoRStats:</v-col>
                                    <v-col>{{overallWinrate}}%</v-col>                        
                                </v-row>
                            </template>
                        </div>
                    </div>
                </v-col>
            </v-row>
        </div>
    </v-app>
</template>
<script>
/*eslint no-console: ["error", { allow: ["log"] }] */
import * as get from "../../utils/get.js";
import Navbar from "./Navbar";
import DeckCard from "../decks/DeckCard";
import CardCard from "../cards/CardCard";

export default {
    components: {
        Navbar,
        DeckCard,
        CardCard
    },
    props: {
        toggleStatsSidebar: { type: Function }
    },
    data () {
        return {
            decks: [],
            regions: [],
            regionNames: {
                SI: "Shadow Isles",
                FR: "Freljord",
                DE: "Demacia",
                IO: "Ionia",
                PZ: "Piltover & Zaun",
                NX: "Noxus"
            },
            regionColors: {
                SI: "teal",
                FR: "light-blue",
                DE: "white",
                IO: "pink",
                PZ: "orange",
                NX: "red",    
            },
            factionsStats: {
                SI: { w: 0, l: 0, wr: 0 },
                FR: { w: 0, l: 0, wr: 0 },
                DE: { w: 0, l: 0, wr: 0 },
                IO: { w: 0, l: 0, wr: 0 },
                PZ: { w: 0, l: 0, wr: 0 },
                NX: { w: 0, l: 0, wr: 0 }
            },
            factionImg: {
                IO: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Ionia.png",
                PZ: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/PitoverZaun.png",
                DE: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Demacia.png",
                NX: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Noxus.png",
                FR: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Freljord.png",
                SI: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/ShadowIsles.png"
            },
            factionRefs: {
                Ionia: "IO",
                PiltoverZaun: "PZ",
                Demacia: "DE",
                Noxus: "NX",
                Freljord: "FR",
                ShadowIsles: "SI"
            },
            isLoading: true,
            isLoadingCards: true,
            minGames: 1,
            cards: [],
            gamesCount: 0,
            decksCount: 0,
            overallWinrate: 0,
            players: [],
        }
    },
    async mounted(){
        let decks = await get.getDecks("", 1000);
        this.decks = decks.data;
        this.decks.map(deck => {
            deck.factions.map(faction => {
                this.factionsStats[faction].w = this.factionsStats[faction].w + deck.wins;
                this.factionsStats[faction].l = this.factionsStats[faction].l + deck.losses;
                this.factionsStats[faction].wr = Math.round(this.factionsStats[faction].w / (this.factionsStats[faction].w + this.factionsStats[faction].l) * 100);
            });
        });
        let regionsSort = [];
        for (let region in this.factionsStats) {
            regionsSort.push([region, this.factionsStats[region].wr]);
        }
        regionsSort.sort(function(a, b) {
            return b[1] - a[1];
        });
        let regionsSorted = [];
        regionsSort.forEach(item => {
            regionsSorted = [ ...regionsSorted, item[0]];
        });
        this.regions = regionsSorted;
        this.decks.forEach(async (deck) => {
          if(!deck.archetypeName){
            if(deck.cardsInDeck.length > 22){
              deck.archetypeName = "Expedition deck";
            }
            else deck.archetypeName = "Offmeta Archetype";
          }
          deck.games = deck.wins+deck.losses;
          deck.games ? deck.winrate = Math.round(deck.wins/(deck.games)*10000)/100 : deck.winrate = 0;
          deck.winrate = parseInt(deck.winrate.toFixed(2));
          deck.champions = [];
          deck.cardsInDeck.forEach(card => {
            if(card.card.rarity === "Champion"){
              deck.champions = [...deck.champions, {code: card.cardCode, cost: card.card.cost}];
            }
          });
          deck.signatureChampion = deck.champions.length ? deck.champions.reduce(function(prev, current) {
              return (prev.cost > current.cost) ? prev : current
          }) : null;
        });
        this.decksCount = this.decks.length;
        let games = await get.getGames();
        let gamesWinner = 0
        let players = {};
        games.data.forEach(game => {
            if(game.playerWon){
                gamesWinner++;
            }
            let playerId = game.playerName.replace(" ", "");
            if(players[playerId]){
                if(game.playerWon){
                    players[playerId].w = players[playerId].w + 1;
                }
                else {
                    players[playerId].l = players[playerId].l + 1;
                }
            }
            else {
                players[playerId] = {id: playerId, name: game.playerName, w:0, l:0, wr:0}
                if(game.playerWon){
                    players[playerId].w = 1;
                    players[playerId].l = 0;
                }
                else {
                    players[playerId].l = 1;
                    players[playerId].w = 0;
                }
            }
            players[playerId].wr = Math.round(players[playerId].w / (players[playerId].w + players[playerId].l) * 100);
        });
        this.players = Object.values(players).sort((a, b) => (a["wr"] > b["wr"]) ? -1 : 1);
        this.gamesCount = games.total;
        this.overallWinrate = Math.round(gamesWinner / this.gamesCount * 100);
        let onlyValidDecks = [];
        this.decks.forEach((deck) => {
          if(deck.cardsInDeck.length > 13 && deck.games >= this.minGames ){
            onlyValidDecks = [...onlyValidDecks, deck];
          }
        });
        this.decks = onlyValidDecks.sort((a, b) => (a["winrate"] > b["winrate"]) ? -1 : 1);
        this.isLoading = false;
        this.isLoadingCards = true;
        let listCards = await get.getCards(500, "", true);
        listCards.data.forEach((card) => {
          card.games = card.wins+card.losses;
          card.games ? card.winrate = Math.round(card.wins/(card.games)*10000)/100 : card.winrate = 0;
          switch(card.region) {
            case "Shadow Isles":
                card.regionImg = "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/ShadowIsles.png";
                break;
            case "Demacia":
                card.regionImg = "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Demacia.png";
                break;
            case "Piltover & Zaun":
                card.regionImg = "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/PitoverZaun.png";
                break;
            case "Noxus":
                card.regionImg = "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Noxus.png";
                break;
            case "Ionia":
                card.regionImg = "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Ionia.png";
                break;
            case "Freljord":
            default:
                card.regionImg = "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Freljord.png";
                break;
          } 
        });
        let onlyValidCards = [];
        listCards.data.forEach((card) => {
          if(card.games >= this.minGames ){
            onlyValidCards = [...onlyValidCards, card];
          }
        })
        this.cards = onlyValidCards.sort((a, b) => (a["winrate"] > b["winrate"]) ? -1 : 1);
        this.isLoadingCards = false;
    } 
}
</script>
<style>
.home-card {
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.7);
    border: 1px solid rgba(255,255,255,0.05);
    border-radius: 15px;
    padding: 20px;
}
.home-row {
    padding: 0 20px;
    min-height: 300px;
    justify-content: center;
}
.home-card .v-card__title {
    padding-top:0px;
}
.home-card-content {
    height: calc(100% - 48px);
    position: relative;
}
.home-card-content .row {
    height: 20%;
}
.home-card-content .row .col {
    padding: 1px;
}
.home-card-content .row .col .home-item {
    height: 100%;
    background-size: cover;
    background-position: 50%;
    position:relative;
    width: 100%;
    min-height: 50px;
}
.home-card-content .row .col .home-item.player-home-item {
    min-height: 35px;
}
.home-card-content .row .col .home-item .home-item-title {
    display:inline-block;
    background-image: linear-gradient(to left, rgba(0,0,0,0) 0%, rgb(0, 0, 0) 20% 80%, rgba(0,0,0,0));
    padding: 2px 25px;
    box-sizing: border-box;
    margin-left: 60px;
    position: relative;
    top: 0px;
    z-index: 2;
    text-transform: none;
    font-size: 12px;
    letter-spacing: 0px;
}
.home-card-content .row .col .home-item .home-item-avatar {
    position: absolute;
    top: 50%;
    margin-top: -24px;
}
.home-card-content .row .col .home-item .home-item-avatar-pre {
    position: absolute;
    top: 50%;
    background: rgba(0,0,0,0.7);
    border-radius: 100%;
    height: 30px;
    width: 30px;
    margin-top: -15px;
}
.home-card-content .row .col .home-item .home-item-avatar-pre .v-avatar {
    margin-top: 3px;
    margin-left: 2px;
}
.home-card-content .row .col .home-item .home-item-avatar .v-avatar .v-image {
    box-sizing: border-box;
    border: 3px solid rgba(255,255,255,0.4);
} 
.home-card-content .row .col .home-item .home-item-avatar:nth-child(1) {
    right:5px;
}
.home-card-content .row .col .home-item .home-item-avatar:nth-child(2) {
    right:40px;
}
.home-card-content .row .col .home-item .home-item-avatar-pre:nth-child(1) {
    left:10px;
}
.home-card-content .row .col .home-item .home-item-avatar-pre:nth-child(2) {
    left:42px;
}
.home-card-content .row .col .home-card-tooltip-wrap:before {
    content: "";
    display: inline-block;
    height: 100%;
    vertical-align: middle;
}
.home-card-content .row .col .home-item .champions, .home-card-content .row .col .home-item .factions {
    position:absolute;
    width:100%;
    height:100%;
    left:0;
    top:0;
}
.home-card-tooltip-wrap {
    display: block;
    width:100%;
    height:100%;
}
.home-item .v-btn__content {
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    overflow:hidden;
}
.deck-card {
    max-width:100% !important;
}
.home-item .cost {
    background-color: #2196f3 !important;
    position: absolute;
    top: 50%;
    font-weight: bold;
    width: 25px;
    height: 25px;
    line-height: 25px;
    text-align: center;
    border-radius: 100%;
    right: 20px;
    margin-top: -12px;
    text-shadow: 0px 0px 1px #000;
}
.home-card-content .row .col .home-item .home-item-title.home-item-title-card {
    margin-left: 45px;
}
.home-card-content .row .col .home-item .home-item-title.home-item-title-player {
    margin-left: 0px;
    background: none;
    padding: 0px;
    position: absolute;
    line-height: 20px;
    top: 50%;
    margin-top: -10px;
    left: 10px;
}
.home-region {
    position:relative;
    padding-left: 45px !important;
}
.home-region .col {
    padding: 0px;
    font-size: 12px;
}
.home-region .v-avatar {
    position: absolute;
    left: -25px;
    top: -4px;
}
.home-global-stats .col {
    background: #090909;
    font-size:12px;
    padding:10px !important;
    margin:1px;
    border-radius: 4px;
}
.home-card-content.home-global-stats .row {
    height:auto;
}
.home-region {
    margin-bottom: 20px;
}
.home-card-content.home-regions, .home-card-content.home-global-stats {
    padding:0 26px;
}
.home-player-winrate {
    position: absolute;
    height: 20px;
    line-height: 20px;
    right: 10px;
    font-size:12px;
    font-weight: bold;
    top: 50%;
    margin-top: -10px;
}
.home-card-decks {
    max-width: 875px !important;
}
.home-card-cards {
    max-width: 625px !important
}
.home-card-regions {
    max-width: 625px !important;
}
.home-card-players {
    max-width: 500px !important;
}
.home-card-global {
    max-width: 375px !important;
}
.home-card-bp-xs, .home-card-bp-sm, .home-card-bp-xs, .home-card-bp-md {
    min-width:100% !important;
    max-width:100% !important;
}
.home-card-bp-xs .home-card-content:not(.home-global-stats) > .row >.col {
    min-width:100%;
}
.home-card-bp-xs .home-card-content > .row, .home-card-bp-sm .home-card-content > .row, .home-card-bp-md .home-card-content > .row {
    height:auto;
}
.home-card-bp-xs .home-card {
    min-height:250px;
}
</style>