<template>
    <v-app :style="`position:relative;z-index:1;background-image:url('assets/${card.cardCode}-full.png');background-size:cover;background-position:50%`">
        <div ref="sidebarBackground" class="sidebar-background" @click="toggleSidebarBackground()"></div>
        <v-row style="margin:0px !important">
            <v-col :style="cardSidebarStyle">
                <v-navigation-drawer 
                    style="z-index:3;transform:none"
                    left
                    class="card-sidebar"
                >
                    <v-btn ref="collapseDeckInfo" style="margin-left: 192px;top: 7px;z-index: 4;margin-bottom: 7px;" @click="toggleFilterSidebar()"><v-icon class="collapse">mdi-arrow-collapse-left</v-icon></v-btn>
                    <v-img :src="'assets/'+card.cardCode+'.png'" height="385px" width="248px" class="ma-2"/>
                    <v-toolbar-title>
                        Trend chart
                    </v-toolbar-title>
                    <v-card style="min-height:150px">
                        <template v-if="isLoading">
                            <div class="loader" style="margin-left:0px;background-image:url('assets/loader.gif')"></div>
                        </template>
                        <template v-else>
                            <GChart
                                type="LineChart"
                                :data="trendValues"
                                :options="{ 
                                    backgroundColor: 'transparent',  
                                    legend: { position: 'none' },
                                    chartArea: {width: '70%', height: '80%'}, 
                                    hAxis: {
                                        textPosition: 'none',
                                        baseline: {
                                            color: '#222'
                                        },
                                        titleColor: '#fff',
                                        title: 'Days',
                                        titleTextStyle: {
                                            fontSize: 12,
                                            italic: false
                                        }
                                    }, 
                                    vAxis: {
                                        textStyle:{color: '#fff'},
                                        gridlineColor: '#222',
                                        titleColor: '#fff',
                                        title: 'Winrate (%)',
                                        titleTextStyle: {
                                            fontSize: 12,
                                            italic: false
                                        }
                                    }
                                }"
                            />
                        </template>
                    </v-card>
                    <template v-if="associatedCards.length">
                        <v-toolbar-title>
                            Associated Cards
                        </v-toolbar-title>
                        <v-list>
                            <v-list-item
                                v-for="card in associatedCards"
                                :key="card.index"
                                :style="`background-image:url('assets/${card.cardCode}-full.jpg')`"
                                class="associated-card"
                                :to="`/card?code=${card.cardCode}`"
                            >
                                <div class="associated-card-background"></div>
                                <v-list-item-title class="associated-card-title">
                                    <b style="margin-right:10px">{{ card.cost }}</b> {{ card.name }}
                                </v-list-item-title>
                            </v-list-item>
                        </v-list>
                    </template>
                    <v-toolbar-title>
                        Details
                    </v-toolbar-title>
                    <v-row>
                        <v-col cols="4" style="padding-left:30px">
                            Region
                        </v-col>
                        <v-col>
                            <v-avatar size="24">
                                <v-img :src="factionImg[card.regionRef]" />    
                            </v-avatar>
                            {{ card.region }}
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col cols="4" style="padding-left:30px">
                            Cost
                        </v-col>
                        <v-col>
                            <v-avatar size="24">
                                <v-icon color="blue">mdi-cards-diamond</v-icon>    
                            </v-avatar>
                            {{ card.cost }}
                        </v-col>
                    </v-row>
                    <template v-if="card.type === 'Unit'">
                        <v-row>
                            <v-col cols="4" style="padding-left:30px">
                                Attack
                            </v-col>
                            <v-col>
                                <v-avatar size="24">
                                    <v-icon color="orange">mdi-sword</v-icon>    
                                </v-avatar>
                                {{ card.attack }}
                            </v-col>
                        </v-row>
                    </template>
                    <template v-if="card.type === 'Unit'">
                        <v-row>
                            <v-col cols="4" style="padding-left:30px">
                                Health
                            </v-col>
                            <v-col>
                                <v-avatar size="24">
                                    <v-icon color="red">mdi-heart</v-icon>    
                                </v-avatar>
                                {{ card.health }}
                            </v-col>
                        </v-row>
                    </template>
                    <v-row>
                        <v-col cols="4" style="padding-left:30px">
                            Rarity
                        </v-col>
                        <v-col v-if="card.rarity">
                            <v-avatar size="24">
                                <v-icon :color="rarities[card.rarity].color">{{ rarities[card.rarity].icon }}</v-icon>    
                            </v-avatar>
                            {{ rarities[card.rarity].text }}
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col cols="4" style="padding-left:30px">
                            Type
                        </v-col>
                        <template v-if="card.type">
                            <template v-if="card.type === 'Unit'">
                                <v-col>
                                    <v-avatar size="24">
                                        <v-icon>mdi-account</v-icon>    
                                    </v-avatar>
                                    Unit
                                </v-col>
                            </template>
                            <template v-else>
                                <v-col>
                                    <v-avatar size="24">
                                        <v-icon>{{ spellsSpeed[card.spellSpeed].icon }}</v-icon>    
                                    </v-avatar>
                                    {{ spellsSpeed[card.spellSpeed].text }}
                                </v-col>
                            </template>
                        </template>
                    </v-row>
                    <v-row v-if="card.type === 'Unit'">
                        <v-col cols="4" style="padding-left:30px">
                            Keywords
                        </v-col>
                        <v-col>
                            <template v-if="card.keywords.length === 0">
                                <v-icon color="grey">mdi-progress-close</v-icon>
                                None
                            </template>
                            <template v-else v-for="keyword in card.keywordRefs">
                                <div style="margin-bottom:10px" :key="keyword.index">
                                    <v-avatar size="24">
                                        <v-icon :color="keywords[keyword].color">{{ keywords[keyword].icon }}</v-icon>    
                                    </v-avatar>
                                    {{ keywords[keyword].text }}
                                </div>
                            </template>
                        </v-col>
                    </v-row>
                </v-navigation-drawer>
            </v-col>
            <v-col style="padding:0px">
                <Navbar class="deck-navbar" :filterSidebar="toggleFilterSidebar" filterTitle="Card" filterIcon="book-information-variant" />
                <v-card style="overflow-x:hidden;overflow-y:scroll;padding:0px;height:calc(100vh - 48px);text-align:center;background-image: linear-gradient(to bottom, rgba(0,0,0,0.5), rgb(0, 0, 0) 80%);">
                    <v-card class="card-title">
                        <v-card-title>{{card.name}}</v-card-title>
                        <div class="flavor-text">{{card.flavorText}}</div>
                    </v-card>
                    <v-row 
                        class="used-in-decks"
                        style="display:block;min-height:200px;text-align:center;margin-left:10px;margin-right:10px"
                    >
                        <v-card-title class="cards-in-deck-title">Decks with {{card.name}}</v-card-title>
                        <template v-if="isLoadingDecks">
                            <div class="loader" style="background-image:url('assets/loader.gif')"></div>
                        </template>
                        <template v-else v-for="deck in decks">
                            <DeckCard :key="deck.index" :deck="deck" />
                        </template>
                    </v-row>
                </v-card>
            </v-col>
        </v-row>
    </v-app>
</template>
<script>
/*eslint no-console: ["error", { allow: ["log"] }] */
import Navbar from "../home/Navbar";
import DeckCard from "../decks/DeckCard";
import * as get from "../../utils/get";
import { GChart } from 'vue-google-charts'

export default {
    components: {
        Navbar,
        DeckCard,
        GChart
    },
    data(){
        return {
            card: {},
            isLoading: false,
            trendLabels: [],
            trendValues: [["Day", "Winrate"]],
            isLoadingDecks: false,
            headers: [
                { text: 'Archetype', value: 'archetype', sortable: false },
                { text: 'Winrate', value: 'winrate' },
                { text: 'Games', value: 'games' },
                { text: '', value: 'detail', sortable: false }
            ],
            decks: [],
            rarities: {
                Champion: { icon: "mdi-hexagon-slice-6", text: "Champion", color: "orange"},
                Epic: { icon: "mdi-pentagon", text: "Epic", color: "purple" },
                Rare: { icon: "mdi-rhombus", text: "Rare", color: "blue" },
                Common: { icon: "mdi-triangle", text: "Common", color: "green" },
            },
            factionImg: {
                Ionia: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Ionia.png",
                PiltoverZaun: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/PitoverZaun.png",
                Demacia: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Demacia.png",
                Noxus: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Noxus.png",
                Freljord: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Freljord.png",
                ShadowIsles: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/ShadowIsles.png"
            },
            spellsSpeed: {
                Burst: { icon: "mdi-octagram-outline", text: "Burst Spell", value: "Burst" },
                Fast: { icon: "mdi-clock-fast", text: "Fast Spell", value: "Fast" },
                Slow: { icon: "mdi-timer-sand", text: "Slow Spell", value: "Slow" },
            },
            keywords: {
                CantBlock: { icon: "mdi-shield-remove", text: "Can't Block", color: "grey" },
                Ephemeral: { icon: "mdi-timer-sand", text: "Ephemeral", color: "purple" },
                Tough: { icon: "mdi-shield-half-full", text: "Tough", color: "yellow" },
                Challanger: { icon: "mdi-hook", text: "Challenger", color: "orange" },
                Elusive: { icon: "mdi-eye-off", text: "Elusive", color: "orange" },
                QuickAttack: { icon: "mdi-sword", text: "Quick Attack", color: "orange" },
                DoubleAttack: { icon: "mdi-sword-cross", text: "Double Attack", color: "orange" },
                Barrier: { icon: "mdi-shield-outline", text: "Barrier", color: "yellow" },
                Fearsome: { icon: "mdi-halloween", text: "Fearsome", color: "orange" },
                Lifesteal: { icon: "mdi-water-outline", text: "Lifesteal", color: "red" },
                Overwhelm: { icon: "mdi-shield-star-outline", text: "Overwhelm", color: "orange" },
            },
            associatedCards: [],
            cardSidebar: true,
            cardSidebarStyle: {
                padding: "0px",
                position: "relative",
                marginLeft: "0px",
                maxWidth: "280px",
                height: "100vh",
                zIndex: 4,
                overflowY: "scroll",
                marginRight: "-10px"
            },
        }
    },
    async mounted(){
        await this.getCard(get.getQueryParameter("code"));
        this.getCardTrend(this.card.cardId);
        this.getListDecks();
        if(this.$vuetify.breakpoint.name === "xs" || this.$vuetify.breakpoint.name === "sm" || this.$vuetify.breakpoint.name === "md"){
            this.toggleFilterSidebar();
            this.cardSidebarStyle.position = "absolute";
            this.$refs["collapseDeckInfo"].$el.style.left="14px";
        }
    },
    methods: {
      toggleSidebarBackground () {
          this.cardSidebarStyle.marginLeft = "-280px";
          this.$refs["sidebarBackground"].style.display = "none";  
          this.cardSidebar = false;
      },
      toggleFilterSidebar () {
        if(this.cardSidebar){
          this.cardSidebarStyle.marginLeft = "-280px";  
          if(this.$vuetify.breakpoint.name === "xs" || this.$vuetify.breakpoint.name === "sm" || this.$vuetify.breakpoint.name === "md"){
            this.$refs["sidebarBackground"].style.display = "none";  
          }
        }
        else {
          this.cardSidebarStyle.marginLeft = "0px";
          if(this.$vuetify.breakpoint.name === "xs" || this.$vuetify.breakpoint.name === "sm" || this.$vuetify.breakpoint.name === "md"){
            this.$refs["sidebarBackground"].style.display = "block";  
          }
        }
        this.cardSidebar = !this.cardSidebar;
      },  
      async getCard(code) {
        this.isLoading = true;
        let card = await get.getCard(code);
        card.associatedCardRefs.map(async code => {
            let associatedCard = await get.getCard(code);
            this.associatedCards = [...this.associatedCards, associatedCard];
        });
        console.log(card);
        this.card = card;
        this.isLoading = false
      },
      async getCardTrend(id){
          this.isLoading = true;
          let trend = await get.getCardTrend(id);
          trend.map(day => {
            let date = day.timestamp.split("T")[0];
            this.trendLabels = [...this.trendLabels, date];
            let games = day.wins+day.losses;
            games = games ? games : 1; 
            let winrate = day.wins/(games)*100;
            this.trendValues = [...this.trendValues, [date, winrate]];
            this.isLoading = false;
          });
      },
      async getListDecks () {
        this.isLoadingDecks = true;
        let listDecks = await get.getDecks(`[{"field": "cardsInDeck.cardCode", "value":"${this.card.cardCode}"}]`, 90);
        listDecks.data.forEach(async (deck) => {
          if(!deck.archetypeName){
            if(deck.cardsInDeck.length > 22){
              deck.archetypeName = "Expedition deck";
            }
            else deck.archetypeName = "Offmeta Archetype";
          }
          deck.games = deck.wins+deck.losses;
          deck.games ? deck.winrate = Math.round(deck.wins/(deck.games)*10000)/100 : deck.winrate = 0;
          deck.winrate = parseInt(deck.winrate.toFixed(2));
          deck.champions = [];
          deck.cardsInDeck.forEach(card => {
            if(card.card.rarity === "Champion"){
              deck.champions = [...deck.champions, {code: card.cardCode, cost: card.card.cost}];
            }
          });
          deck.signatureChampion = deck.champions.length ? deck.champions.reduce(function(prev, current) {
              return (prev.cost > current.cost) ? prev : current
          }) : null;
        });
        let onlyValidDecks = [];
        listDecks.data.forEach((deck) => {
          if(deck.cardsInDeck.length > 13){
            onlyValidDecks = [...onlyValidDecks, deck];
          }
        })
        this.decks = onlyValidDecks.sort((a, b) => (a["games"] > b["games"]) ? -1 : 1);
        this.isLoadingDecks = false;
      }
    },
}
</script>
<style scoped>
.card-background {
    width: 100%;
    height: auto;
    -webkit-mask-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,0.2)), to(rgba(0,0,0,0)));
    mask-image: linear-gradient(to bottom,
    rgba(0,0,0,0.2),
    rgba(0,0,0,0));
    position: absolute;
}
.card-sidebar .v-avatar{
    margin-top: -3px;
}
.associated-card {
    background-position:50%;
    background-size:cover;
    margin-bottom: 2px;
}
.associated-card::before {
    opacity: 0 !important;
}
.associated-card-background {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background:rgba(0,0,0,0.5);
}
.associated-card-title {
    z-index:3;
    font-size:12px !important;
    font-weight: bold;
}
.associated-card-title b {
    background-color: #2196f3 !important;
    border-radius: 100%;
    display:inline-block;
    width:20px;
    height:20px;
    line-height: 20px;
    text-align: center;
}
.card-title {
    background-color: rgba(0,0,0,0.7) !important;
    display: inline-block;
    margin-top: 10px;
    padding: 20px;
    border-radius: 15px !important;
    border: 1px solid rgba(255,255,255,0.1);
    box-shadow: 0 5px 5px rgba(0,0,0,0.6) !important;
    max-width: calc(100% - 17px) !important;
}
.card-title .v-card__title {
    padding-top: 0px;
    padding-left: 0px;
    font-size: 26px;
    text-align: center !important;
    width:100%;
    display:inline-block;
}
.flavor-text {
    width: 600px;
    font-size: 18px;
    font-style: italic;
    font-weight: normal;
    max-width: 100%;
}
.cards-in-deck-title {
    display: block;
    width: calc(100% - 10px);
    background-image: linear-gradient(to left, rgba(0,0,0,0), rgba(0,0,0,0.8), rgba(0,0,0,0));
    margin-top: 10px;
}
</style>