<template>
    <v-row>
      <div ref="sidebarBackground" class="sidebar-background" @click="toggleSidebarBackground()"></div>
      <v-col :style="filterCardSidebarStyle">
        <v-navigation-drawer 
          style="z-index:3;transform:none"
          ref="navbar"
          class="filter-sidebar"
        >
          <v-btn style="margin-left: 202px;top: 7px;z-index: 4;margin-bottom:6px;" @click="toggleFilterCardSidebar()"><v-icon>mdi-arrow-collapse-left</v-icon></v-btn>
          <v-toolbar-title>
              Region
          </v-toolbar-title>
          <v-slide-group
            v-model="filterCard.factions"
            multiple
            class="factions"
            @change="applyFilter"
            max=6
          >
            <v-slide-item
              v-for="n in 6"
              :key="n"
              v-slot:default="{ active, toggle }"
            >
              <v-card
                :color="active ? 'primary' : 'dark'"
                class="ma-1"
                height="28"
                width="28"
                @click="toggle"
                :img="factionImg[n-1]"
              >
                <v-row
                  class="fill-height"
                  align="center"
                  justify="center"
                >
                  <v-scale-transition>
                    <v-icon
                      v-if="active"
                      color="white"
                      size="16"
                      v-text="'mdi-close-circle-outline'"
                    ></v-icon>
                  </v-scale-transition>
                </v-row>
              </v-card>
            </v-slide-item>
          </v-slide-group>
          <v-toolbar-title>
              Cost
          </v-toolbar-title>
          <v-btn-toggle
            v-model="filterCard.cost"
            dense
            dark
            multiple
            @change="applyFilter"
            class="cost"
          >
            <v-btn>0</v-btn>
            <v-btn>1</v-btn>
            <v-btn>2</v-btn>
            <v-btn>3</v-btn>
            <v-btn>4</v-btn>
            <v-btn>5</v-btn>
            <v-btn>6</v-btn>
            <v-btn>7+</v-btn>
          </v-btn-toggle>
          <v-toolbar-title>
              Attack
          </v-toolbar-title>
          <v-btn-toggle
            v-model="filterCard.attack"
            dense
            dark
            multiple
            @change="applyFilter"
            class="cost"
          >
            <v-btn>0</v-btn>
            <v-btn>1</v-btn>
            <v-btn>2</v-btn>
            <v-btn>3</v-btn>
            <v-btn>4</v-btn>
            <v-btn>5</v-btn>
            <v-btn>6</v-btn>
            <v-btn>7+</v-btn>
          </v-btn-toggle>
          <v-toolbar-title>
              Health
          </v-toolbar-title>
          <v-btn-toggle
            v-model="filterCard.health"
            dense
            dark
            multiple
            @change="applyFilter"
            class="cost"
          >
            <v-btn>0</v-btn>
            <v-btn>1</v-btn>
            <v-btn>2</v-btn>
            <v-btn>3</v-btn>
            <v-btn>4</v-btn>
            <v-btn>5</v-btn>
            <v-btn>6</v-btn>
            <v-btn>7+</v-btn>
          </v-btn-toggle>
          <v-toolbar-title>
              Rarity
          </v-toolbar-title>
          <v-list>
            <v-list-item-group v-model="filterCard.rarity" multiple class="type" @change="applyFilter">
              <v-list-item
                v-for="(rarity, i) in rarities"
                :key="i"
                :value="rarity.text"
              >
                <v-list-item-icon>
                  <v-icon v-text="rarity.icon" :color="rarity.color"></v-icon>
                </v-list-item-icon>

                <v-list-item-content>
                  <v-list-item-title v-text="rarity.text"></v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>
          </v-list>
          <v-toolbar-title>
              Type
          </v-toolbar-title>
          <v-list>
            <v-list-item-group v-model="filterCard.type" multiple class="type" @change="applyFilter">
              <v-list-item
                v-for="(type, i) in types"
                :key="i"
                :value="type.value"
              >
                <v-list-item-icon>
                  <v-icon v-text="type.icon"></v-icon>
                </v-list-item-icon>

                <v-list-item-content>
                  <v-list-item-title v-text="type.text"></v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>
          </v-list>
          <v-toolbar-title>
              Keyword
          </v-toolbar-title>
          <v-list>
            <v-list-item-group v-model="filterCard.keyword" multiple class="type" @change="applyFilter">
              <v-list-item
                v-for="(keyword, i) in keywords"
                :key="i"
                :value="keyword.text"
              >
                <v-list-item-icon>
                  <v-icon v-text="keyword.icon" :color="keyword.color"></v-icon>
                </v-list-item-icon>

                <v-list-item-content>
                  <v-list-item-title v-text="keyword.text"></v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>
          </v-list>
        </v-navigation-drawer>
      </v-col>
      <v-col style="padding:0px;position:relative">
        <Navbar :filterSidebar="toggleFilterCardSidebar"  filterTitle="Filter" filterIcon="tune" />
        <v-card-title style="margin-left:15px;">
            Legends of Runeterra Cards
        </v-card-title>
        <v-card-subtitle style="margin-left:15px;">
            {{cards.length}} cards matching the filter
        </v-card-subtitle>
        <template v-if="isLoading">
          <div class="loader" style="background-image:url('assets/loader.gif')"></div>
        </template>
        <template v-else>
          <div style="margin:0 auto;display:inline-block;height:calc(100vh - 110px);overflow: scroll;text-align:center">
            <CardCard v-for="card in cards" :key="card.index" :card="card" />
            <br />
            <br />
          </div>
        </template>
      </v-col>
    </v-row>
</template>

<script>
/*eslint no-console: ["error", { allow: ["log"] }] */
  import * as get from "../../utils/get.js";
  import Navbar from "../home/Navbar";
  import CardCard from "./CardCard";

  export default {
    components: {
      Navbar,
      CardCard
    },
    props: {
        filter: { type: String }
    },
    data () {
      return {
        headers: [
          { text: '', value: 'cardCode', sortable: false },
          { text: 'Card Name', value: 'name', sortable: true },
          { text: 'Winrate', value: 'winrate', sortable: true },
          { text: 'Games', value: 'games', sortable: true },
          { text: 'Copies used', value: 'copies', sortable: true },
          { text: 'Rarity', value: 'rarity', sortable: false },
          { text: 'Region', value: 'region', sortable: true },
          { text: 'Description', value: 'descriptionRaw', sortable: false },
          { text: '', value: 'detail', sortable: false },
        ],
        cards: [],
        allCards: [],
        isLoading: false,
        search: "",
        filterCardSidebar: true,
        filterCardSidebarStyle: {
          padding: "0px",
          position: "relative",
          marginLeft: "0px",
          maxWidth: "280px",
          height: "100vh",
          zIndex: 4
        },
        filterCard: {
          factions: [],
          cost: [],
          type: [],
          rarity: [],
          keyword: [],
          attack: [],
          health: []
        },
        factionImg: [
          "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Ionia.png",
          "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/PitoverZaun.png",
          "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Demacia.png",
          "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Noxus.png",
          "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Freljord.png",
          "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/ShadowIsles.png",
        ],
        regionNames: [
            "Ionia",
            "Piltover & Zaun",
            "Demacia",
            "Noxus",
            "Freljord",
            "Shadow Isles"
        ],
        types: [
          { icon: "mdi-account", text: "Unit", value: "Unit" },
          { icon: "mdi-octagram-outline", text: "Burst Spell", value: "Burst" },
          { icon: "mdi-clock-fast", text: "Fast Spell", value: "Fast" },
          { icon: "mdi-timer-sand", text: "Slow Spell", value: "Slow" },
        ],
        rarities: [
          { icon: "mdi-hexagon-slice-6", text: "Champion", color: "orange"},
          { icon: "mdi-pentagon", text: "Epic", color: "purple" },
          { icon: "mdi-rhombus", text: "Rare", color: "blue" },
          { icon: "mdi-triangle", text: "Common", color: "green" },
        ],
        keywords: [
          { icon: "mdi-shield-remove", text: "Can't Block", color: "grey" },
          { icon: "mdi-timer-sand", text: "Ephemeral", color: "purple" },
          { icon: "mdi-shield-half-full", text: "Tough", color: "yellow" },
          { icon: "mdi-heart-broken", text: "Last Breath", color: "light-blue" },
          { icon: "mdi-hook", text: "Challenger", color: "orange" },
          { icon: "mdi-snowflake", text: "Frostbite", color: "purple" },
          { icon: "mdi-eye-off", text: "Elusive", color: "orange" },
          { icon: "mdi-sword", text: "Quick Attack", color: "orange" },
          { icon: "mdi-sword-cross", text: "Double Attack", color: "orange" },
          { icon: "mdi-cards", text: "Play", color: "yellow" },
          { icon: "mdi-undo-variant", text: "Recall", color: "yellow" },
          { icon: "mdi-shield-outline", text: "Barrier", color: "yellow" },
          { icon: "mdi-bullseye", text: "Stun", color: "purple" },
          { icon: "mdi-halloween", text: "Fearsome", color: "orange" },
          { icon: "mdi-lifebuoy", text: "Support", color: "yellow" },
          { icon: "mdi-numeric-10-circle", text: "Enlightened", color: "yellow" },
          { icon: "mdi-water-outline", text: "Lifesteal", color: "red" },
          { icon: "mdi-star-three-points-outline", text: "Strike", color: "yellow" },
          { icon: "mdi-shield-star-outline", text: "Overwhelm", color: "orange" },
          { icon: "mdi-account-group", text: "Allegiance", color: "yellow" },
          { icon: "mdi-water", text: "Drain", color: "yellow" },
        ]
      }
    },
    mounted () {
      this.getListCards(`[{"field":"collectible", "value":true}]`);
      if(this.$vuetify.breakpoint.name === "xs" || this.$vuetify.breakpoint.name === "sm" || this.$vuetify.breakpoint.name === "md"){
        this.toggleFilterCardSidebar();
        this.filterCardSidebarStyle.position = "absolute";
      }
    },
    methods: {
      toggleSidebarBackground () {
          this.filterCardSidebarStyle.marginLeft = "-280px";
          this.$refs["sidebarBackground"].style.display = "none";  
          this.filterCardSidebar = false;
      },
      toggleFilterCardSidebar () {
        if(this.filterCardSidebar){
          this.filterCardSidebarStyle.marginLeft = "-280px";  
          if(this.$vuetify.breakpoint.name === "xs" || this.$vuetify.breakpoint.name === "sm" || this.$vuetify.breakpoint.name === "md"){
            this.$refs["sidebarBackground"].style.display = "none";  
          }
        }
        else {
          this.filterCardSidebarStyle.marginLeft = "0px";
          if(this.$vuetify.breakpoint.name === "xs" || this.$vuetify.breakpoint.name === "sm" || this.$vuetify.breakpoint.name === "md"){
            this.$refs["sidebarBackground"].style.display = "block";  
          }
        }
        this.filterCardSidebar = !this.filterCardSidebar;
      },
      async getListCards (filter = "") {
        this.isLoading = true;
        let listCards = await get.getCards(500, filter, false);
        listCards.data.forEach((card) => {
          card.games = card.wins+card.losses;
          card.games ? card.winrate = Math.round(card.wins/(card.games)*10000)/100 : card.winrate = 0;
        });
        listCards.data = listCards.data.sort((a, b) => (a["region"] > b["region"]) ? 1 : -1);
        listCards.data = listCards.data.sort((a, b) => (a["cost"] > b["cost"]) ? 1 : -1);
        this.cards = listCards.data;
        this.allCards = listCards.data;
        this.isLoading = false
      },
      async applyFilter () {
        let cards = this.allCards;
        if(this.filterCard.factions.length){
          let factions = this.filterCard.factions.map(faction => {
            return this.regionNames[faction];
          });
          cards = this.allCards.filter((card) => {
            return factions.includes(card.region);
          });
        }
        if(this.filterCard.cost.length){
          if(this.filterCard.cost.includes(7)){
            if(this.filterCard.cost.includes(8)){
              null;
            }
            else {
              this.filterCard.cost.push(8,9,10,11,12);
            }
          }
          else {
            this.filterCard.cost = this.filterCard.cost.filter(cost => {
              return cost !== 8 && cost !== 9 && cost !== 10 && cost !== 11 && cost !== 12;
            });
          }
          if(this.filterCard.cost.length){
            cards = cards.filter(card => {
              return this.filterCard.cost.includes(card.cost);
            });
          }
        }
        if(this.filterCard.attack.length){
          if(this.filterCard.attack.includes(7)){
            if(this.filterCard.attack.includes(8)){
              null;
            }
            else {
              this.filterCard.attack.push(8,9,10,11,12);
            }
          }
          else {
            this.filterCard.attack = this.filterCard.attack.filter(attack => {
              return attack !== 8 && attack !== 9 && attack !== 10 && attack !== 11 && attack !== 12;
            });
          }
          if(this.filterCard.attack.length){
            cards = cards.filter(card => {
              return this.filterCard.attack.includes(card.attack);
            });
          }
        }
        if(this.filterCard.health.length){
          if(this.filterCard.health.includes(7)){
            if(this.filterCard.health.includes(8)){
              null;
            }
            else {
              this.filterCard.health.push(8,9,10,11,12);
            }
          }
          else {
            this.filterCard.health = this.filterCard.health.filter(health => {
              return health !== 8 && health !== 9 && health !== 10 && health !== 11 && health !== 12;
            });
          }
          if(this.filterCard.health.length){
            cards = cards.filter(card => {
              return this.filterCard.health.includes(card.health);
            });
          }
        }
        if(this.filterCard.type.length){
          cards = cards.filter(card => {
            return this.filterCard.type.includes(card.type) || this.filterCard.type.includes(card.spellSpeed);
          })
        }
        if(this.filterCard.rarity.length){
          cards = cards.filter(card => {
            return this.filterCard.rarity.includes(card.rarity);
          })
        }
        if(this.filterCard.keyword.length){
          if(this.filterCard.keyword.includes("Discard")){
            if(this.filterCard.keyword.includes("discard")){
              null;
            }
            else {
              this.filterCard.keyword.push("discard");
            }
          }
          else {
            this.filterCard.keyword = this.filterCard.keyword.filter(keyword => {
              return keyword !== "discard";
            });
          }
          cards = cards.filter(card => {
            return card.keywords.some(keyword => this.filterCard.keyword.indexOf(keyword) >= 0) || new RegExp(this.filterCard.keyword.join("|")).test(card.description);
          })
        }
        this.cards = cards;
      }
    },
    watch: {
      filter: {
        handler: function(val){
          console.log(val);
          this.getListCards(val)
        },
        deep: true
      },
    }
  }
</script>
<style>
  .v-slide-group__content {
    margin-left: -5px !important;
  }
  .cost .v-btn:not(.v-btn--round).v-size--default {
    min-width: 48px !important;
    border-radius:100% !important;
    margin-left: 5px;
    height: 48px;
    margin-bottom: 5px;
  }
  .cost {
    flex-wrap: wrap;
    max-width: 220px !important;
    margin-left: 35px;
    margin-top: 9px;
    background:transparent !important;
  }
  .cost .v-item--active {
    background-color: #2196f3 !important
  }
  .type {
    max-width: 250px;
    margin-left: 20px;
  }
  .type .v-list-item {
    border-bottom: 1px solid rgba(255,255,255,0.1);
  }
  .type .v-list-item:first-child {
    border-top: 1px solid rgba(255,255,255,0.1);
  }
  .type .v-list-item--active {
    background-color: #2196f3 !important
  }
  .type .v-list-item__icon {
    margin: 11px 0;
  }
  .type .v-list-item--active .v-icon {
    color: #fff !important;
  }
</style>